body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  min-width: 320px;
  background:
    radial-gradient(circle farthest-corner at 35% 90%, #fec564, transparent 50%),
    radial-gradient(circle farthest-corner at 0 140%, #fec564, transparent 50%),
    radial-gradient(ellipse farthest-corner at 0 -25%, #5258cf, transparent 50%),
    radial-gradient(ellipse farthest-corner at 20% -50%, #5258cf, transparent 50%),
    radial-gradient(ellipse farthest-corner at 100% 0, #893dc2, transparent 50%),
    radial-gradient(ellipse farthest-corner at 60% -20%, #893dc2, transparent 50%),
    radial-gradient(ellipse farthest-corner at 100% 100%, #d9317a, transparent),
    -webkit-gradient(linear, left top, left bottom, from(#6559ca), color-stop(30%, #bc318f), color-stop(50%, #e33f5f), color-stop(70%, #f77638), to(#fec66d));
  background:
    -webkit-radial-gradient(circle farthest-corner at 35% 90%, #fec564, transparent 50%),
    -webkit-radial-gradient(circle farthest-corner at 0 140%, #fec564, transparent 50%),
    -webkit-radial-gradient(ellipse farthest-corner at 0 -25%, #5258cf, transparent 50%),
    -webkit-radial-gradient(ellipse farthest-corner at 20% -50%, #5258cf, transparent 50%),
    -webkit-radial-gradient(ellipse farthest-corner at 100% 0, #893dc2, transparent 50%),
    -webkit-radial-gradient(ellipse farthest-corner at 60% -20%, #893dc2, transparent 50%),
    -webkit-radial-gradient(ellipse farthest-corner at 100% 100%, #d9317a, transparent),
    -webkit-linear-gradient(#6559ca, #bc318f 30%, #e33f5f 50%, #f77638 70%, #fec66d 100%);
  background:
    -o-radial-gradient(circle farthest-corner at 35% 90%, #fec564, transparent 50%),
    -o-radial-gradient(circle farthest-corner at 0 140%, #fec564, transparent 50%),
    -o-radial-gradient(ellipse farthest-corner at 0 -25%, #5258cf, transparent 50%),
    -o-radial-gradient(ellipse farthest-corner at 20% -50%, #5258cf, transparent 50%),
    -o-radial-gradient(ellipse farthest-corner at 100% 0, #893dc2, transparent 50%),
    -o-radial-gradient(ellipse farthest-corner at 60% -20%, #893dc2, transparent 50%),
    -o-radial-gradient(ellipse farthest-corner at 100% 100%, #d9317a, transparent),
    -o-linear-gradient(#6559ca, #bc318f 30%, #e33f5f 50%, #f77638 70%, #fec66d 100%);
  background:
    radial-gradient(circle farthest-corner at 35% 90%, #fec564, transparent 50%),
    radial-gradient(circle farthest-corner at 0 140%, #fec564, transparent 50%),
    radial-gradient(ellipse farthest-corner at 0 -25%, #5258cf, transparent 50%),
    radial-gradient(ellipse farthest-corner at 20% -50%, #5258cf, transparent 50%),
    radial-gradient(ellipse farthest-corner at 100% 0, #893dc2, transparent 50%),
    radial-gradient(ellipse farthest-corner at 60% -20%, #893dc2, transparent 50%),
    radial-gradient(ellipse farthest-corner at 100% 100%, #d9317a, transparent),
    linear-gradient(#6559ca, #bc318f 30%, #e33f5f 50%, #f77638 70%, #fec66d 100%);
}


